import './App.css';
import { useState, useEffect } from 'react';
import { ethers } from 'ethers';
import { BiLoaderAlt } from 'react-icons/bi';
import { SiTwitter } from 'react-icons/si';
import { SiDiscord } from 'react-icons/si';
import FlyFrogs from './artifacts/contracts/FlyFrogs.sol/FlyFrogs.json';
import frog1 from './images/burger.png';

// Update with the contract address logged out to the CLI when it was deployed
const flyfrogsAddress = "0x31d4Da52c12542AC3D6aAdbA5ed26a3A563a86Dc";
const openSeaLink = "https://opensea.io/";

function App() {
  // store quantity in local state
  const [quantity, setQuantity] = useState(1);
  const [minting, setMinting] = useState(false);
  const [frogIds, setFrogIds] = useState([]);
  const [account, setAccount] = useState();
  const [isPaused, setIsPaused] = useState(false);
  const [statusAlert, setStatusAlert] = useState();
  
  useEffect(() => {
    getIsPaused();
  });
  
  // request access to the user's MetaMask account
  async function getAccount() {
    if (typeof window.ethereum !== 'undefined') {
      const [account] = await window.ethereum.request({ method: 'eth_requestAccounts' });
      setAccount(account);
      getFlyFrogs();
    }
  }
  
  async function getIsPaused() {
    const provider = ethers.getDefaultProvider();
    const contract = new ethers.Contract(flyfrogsAddress, FlyFrogs.abi, provider);
    try {
      const paused = await contract.isPaused();
      setIsPaused(paused);
    } catch (e) {
      console.log(e);
    }
  }

  async function getFlyFrogs() {
    if (typeof window.ethereum !== 'undefined') {
      const [account] = await window.ethereum.request({ method: 'eth_requestAccounts' });
      const provider = new ethers.providers.Web3Provider(window.ethereum);
      const contract = new ethers.Contract(flyfrogsAddress, FlyFrogs.abi, provider);
      let frogIds = [];
      try {
        const balance = await contract.balanceOf(account);
        for (let tokenIndex = 0; tokenIndex < balance; tokenIndex++) {
            const tokenId = await contract.tokenOfOwnerByIndex(account, tokenIndex);
            frogIds.push(tokenId.toString());
        }
      } catch (e) {
        console.log(e);
      }
      setFrogIds(frogIds);
    }
  }

  async function mintFrogs() {
    if (typeof window.ethereum !== 'undefined') {
      setMinting(true);
      const [account] = await window.ethereum.request({ method: 'eth_requestAccounts' });
      const provider = new ethers.providers.Web3Provider(window.ethereum);
      const signer = provider.getSigner();
      const contract = new ethers.Contract(flyfrogsAddress, FlyFrogs.abi, signer);
      const total = (quantity * 0.022).toString();
      try {
        const transaction = await contract.mint(account, quantity, {value: ethers.utils.parseEther(total)});
        await transaction.wait();
        setStatusAlert(null);
      } catch (e) {
        console.log(e.message);
        setStatusAlert("Oops, something went wrong. Check funds and try again.");
        setMinting(false);
      }
      setMinting(false);
      getFlyFrogs();
      console.log(`${quantity} Frogs successfully minted to ${account}`);
    } else {
      setStatusAlert('Please connect with MetaMask.');
    }
  }
  
  const  renderFrogs = frogs => {
    let message = `Congrats! You are the proud owner of ${frogs.length} Fly Frog.`
    if (parseInt(frogs.length) > 1) {
      message = `Congrats! You are the proud owner of ${frogs.length} Fly Frogs.`
    }
    const images = frogs.map((x) =>
      <li><a href={openSeaLink+'assets/'+flyfrogsAddress+"/"+x} target="_blank" rel="noreferrer"><img src={"https://gateway.pinata.cloud/ipfs/QmTubGm6aKcjgTutgk7whbhXKeW42dHQmdXwp4GbbWxtTF/flyfrog"+x+".png"} alt={"Fly Frog "+x} width="200" /></a></li>
    );
    return (
      <div className="Frog-pond">
        <p>
          {message} <a className="App-link" href={openSeaLink+account+'/fly-frogs'} target="_blank" rel="noreferrer">View on OpenSea â†’</a>
        </p>
        <ul className="Frog-pond-list">{images}</ul>
      </div>);
  }
  
  const getQuantityContent = max => {
    let content = [];
    for (let i = 1; i <= max; i++) {
      content.push(<option key={i} value={i}>{i}</option>);
    }
    return content;
  }

  return (
    <div className="App">
      <header className="App-header">
        <div className="App-container">
          <div className="flex">
            <div className="flex-item">
              <button onClick={getAccount}>{account ? account.substring(0, 6) + '...' + account.substring(38) : 'Connect Wallet'}</button>
              <h1 className="App-heading">Fly Frogs</h1>
              <p>The future is amphibious, ribbit.</p>
            </div>
            <div className="flex-item">
              <img src={frog1} alt="Fly Frog with burger hat" />
            </div>
          </div>
        </div>
      </header>
      <section className="App-mint">
        <div className="App-container">
            <div>
                <p>
                  {isPaused ? <strong>Minting begins Sept. 3 at 4pm EST!<br /></strong>: null}
                  Price: 0.022 ETH + gas. Limit 30 per transaction.
                </p>
                {statusAlert ? <p className="error">{statusAlert}</p> : null}
                {isPaused
                  ? <div>
                    <select disabled value={quantity}>
                      {getQuantityContent(30)}
                    </select>
                    <button disabled>
                      Mint!
                    </button>
                  </div>
                  : <div>
                    <select onChange={e => setQuantity(e.target.value)} value={quantity}>
                      {getQuantityContent(30)}
                    </select>
                    <button onClick={mintFrogs}>
                      {minting ? <BiLoaderAlt className="spinning" /> : "Mint!"}
                    </button>
                  </div>
                }
              </div>
          {frogIds.length > 0 ? renderFrogs(frogIds) : ''}
        </div>
      </section>
      <section className="App-about">
        <div className="App-container">
          <h2>Come on get hoppy!</h2>
          <p>
            Fly Frogs are a collection of 10,000 randomly generated NFTs. No two frogs are exactly alike. The frogs are composed of 180+ hand-drawn assets across 6 traits:
          </p>
          <ul>
            <li>backgrounds (25)</li>
            <li>skins (23)</li>
            <li>heads (52)</li>
            <li>eyes (14)</li>
            <li>mouths (17)</li>
            <li>outfits (56)</li>
          </ul>
          <p>Fly Frogs will be available to purchase from this website starting <strong>Friday Sept. 3 at 4PM EST</strong>.</p>
          <p>It costs 0.022 ETH to mint a Fly Frog. Limit 30 frogs per transaction. Fly Frogs will be revealed instantly after minting.</p>
          <p>We&rsquo;re reserving some Fly Frogs for promotions and giveaways. Hop on over to <a href="https://twitter.com/FlyFrogsNFT" target="_blank" className="App-link" rel="noreferrer">Twitter</a> and <a href="http://discord.gg/kfp4xftqKM" target="_blank" className="App-link" rel="noreferrer">Discord</a> to join the Fly Frog community!</p>
        </div>
      </section>
      <footer className="App-footer">
        <div className="App-container">
          <a href="https://etherscan.io/address/0x31d4Da52c12542AC3D6aAdbA5ed26a3A563a86Dc" target="_blank" rel="noreferrer">Smart Contract</a>
          <a href="https://twitter.com/FlyFrogsNFT" target="_blank" rel="noreferrer"><SiTwitter /></a>
          <a href="http://discord.gg/kfp4xftqKM" target="_blank" rel="noreferrer"><SiDiscord /></a>
          <a href="https://opensea.io/collection/fly-frogs" target="_blank" rel="noreferrer">OpenSea</a>
        </div>
      </footer>
    </div>
  );
}

export default App;